export default {
	data() {
		return {
			iconType: ['clear'],
			user: false,//账号图标显示
			pswd: false,//密码图标显示
			userText: '',
			pswdText: '',
			pswdType: true
		}
	},
	onLoad() {
		
	},
	methods: {
		inputChange (e, val) {
			if (val == 'user') {
				e.detail.value ? this.user = true : this.user = false;
				this.userText = e.detail.value;
			} else if (val == 'pswd') {
				if (e.detail.value) {
					this.pswd= true;
				} else {
					this.pswd = false;
				}
				this.pswdText = e.detail.value;
			}
		},
		clearUser () {
			if (this.user) {
				this.userText = '';
				this.user = false;
			} else {
				this.user = true;
			}
		},
		toViewPswd () {
			this.pswdType = !this.pswdType;
		},
		handleLoginBtn (val) {
			if (val == '注册账号') {
				uni.navigateTo({
					url: '../registered/registered'
				})
			} else if (val == '忘记密码') {
				uni.navigateTo({
					url: '../uploadPswd/uploadPswd'
				})
			}
		},
		handleLogin () {
			if (!this.userText) {
				uni.showToast({
					icon: 'none',
					title: '请输入账号'
				});
				return false;
			} else if (!this.pswdText) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return false;
			} else {
				if (this.userText != uni.getStorageSync('user')) {
					uni.showToast({
						icon: 'none',
						title: '账号错误'
					});
					return false;
				} else if (this.pswdText != uni.getStorageSync('pswd')) {
					uni.showToast({
						icon: 'none',
						title: '密码错误'
					});
					return false;
				} else {
					uni.showToast({
						icon: 'none',
						title: '登录成功'
					});
					uni.switchTab({
						url: '../home/home'
					})
				}
			}
		}
	}
}
